body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
  background-color: #f5f0f6;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  height: 100vh;
}

.App-header {
  background-color: #03A9F4;
  padding: 10px;
  color: white;
}

.App-header-container {
  -webkit-flex: 0 1 auto;
          flex: 0 1 auto;
}

.App-title {
  font-size: 26px;
}

.App-intro {
  font-size: large;
}

.App-container {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  -webkit-flex-flow: column;
          flex-flow: column;
  display: -webkit-flex;
  display: flex;
}

.App-messages {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 20 1 auto;
          flex: 20 1 auto;
  -webkit-flex-direction: column-reverse;
          flex-direction: column-reverse;
  background-color: #f5f0f6;
  padding: 20px;
  overflow-y: scroll;
}

.App-moniker {
  border-bottom: 1px solid rgba(0,0,0,0.1);
  -webkit-flex: 1 1;
          flex: 1 1;
}

.message-form {
  -webkit-flex-direction: row;
          flex-direction: row;
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.App-message-entry {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.App-message-entry input {
  border: 1px solid #ced4da;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  border-radius: 8px;
  font-size: 20px;
}

.App-submit-button input {
  height: 40px;
  font-size: 24px;
  background-color: #007bff;
  border-color: #007bff;
  border: 1px solid transparent;
  border-radius: 8px;
  color: white;
}

.logo-text {
  color: #fe5f55;
}

.slogan-text {
  color: #2b4162;
}

.dot {
  background-color: #ef1062;
  height: 0.8em;
  width: 0.8em;
  border-radius: 1em;
  -webkit-animation: light 0.5s infinite;
          animation: light 0.5s infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

.green-dot {
  background-color: #3AB795;
}

.green-text {
  color: #3AB795;
}

.counter {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

@-webkit-keyframes light {
  from {
    opacity:0
  }
  to {
    opacity:100
  }
}

@keyframes light {
  from {
    opacity:0
  }
  to {
    opacity:100
  }
}

.message-container {
  background-color: rgba(255,255,255,0.5);
  border: 1px solid rgba(0,0,0,0.2);

  overflow-wrap: break-word;
  word-wrap: break-word;

  -ms-word-break: break-all;
  /* This is the dangerous one in WebKit, as it breaks things wherever */
  word-break: break-all;
  /* Instead use this non-standard one: */
  word-break: break-word;

  /* Adds a hyphen where the word breaks, if supported (No Blink) */
  -ms-hyphens: auto;
  -webkit-hyphens: auto;
  hyphens: auto;
}

.form-container {
  -webkit-flex: 0 1 auto;
          flex: 0 1 auto;
}
